import React, { useContext, useEffect } from "react";
import Plx from "react-plx";
import SoftwareNavbar from "../../Components/SoftwareComponents/SoftwareNavbar/SoftwareNavbar";
import AOS from "aos";
import "aos/dist/aos.css";
import SoftwareTitle from "../../Components/SoftwareComponents/SoftwareTitle/SoftwareTitle";
import SoftwareInnerPages from "../../Components/SoftwareComponents/SoftwareInnerPages/SoftwareInnerPages";
import SoftwareFeature from "../../Components/SoftwareComponents/SoftwareFeature/SoftwareFeature";
import SoftwareAllDesigns from "../../Components/SoftwareComponents/SoftwareAllDesigns/SoftwareAllDesigns";
import SofwareBanner from "../../Components/SoftwareComponents/SofwareBanner/SofwareBanner";
import SoftwareTestimonials from "../../Components/SoftwareComponents/SoftwareTestimonials/SoftwareTestimonials";
import SoftwareFooter from "../../Components/SoftwareComponents/SoftwareFooter/SoftwareFooter";
import SoftwarePagesGroup from "../../Components/SoftwareComponents/SoftwarePagesGroup/SoftwarePagesGroup";
import ScrollToTop from "../../Components/ScrollToTop/ScrollToTop";
import Navbar from "../../Components/MasterPage/Navbar/Navbar";
import { useTranslation } from "react-i18next";
import { loadingContext } from "../../context/LoadingContext";
import { RingLoader } from "react-spinners";

export default function Software() {
  const { i18n, t, languageDirection } = useTranslation();
      let { isLoading, load } = useContext(loadingContext);
  useEffect(() => {
    AOS.init({
      duration: 2000,
    });
    AOS.refresh();
  }, []);
  return (
    <>
              {isLoading ? (
            <div className="d-flex justify-content-center align-items-center vh-100 z-3">
              <RingLoader color="#2196F3" width="500" />
            </div>
          ) : (<> <ScrollToTop style="scroll-to-top software-color" />
      {/* start software banner */}
      <section>
        <div className="software-banner ">
          <Navbar />
          <SofwareBanner />
        </div>
      </section>
      {/* end software banner */}
      {/* start display demo */}
      <section className="py-5">
        <SoftwareTitle
          title={t("software-pages-title")}
          subtitle={t("software-pages-subtitle")}
        />
        <SoftwarePagesGroup />
      </section>
      {/* end display demo */}
      {/* start display pages */}
      <section className="py-5">
        <SoftwareTitle
          title={t("clients-and-partner")}
          subtitle={t("clients-and-partner-desc")}
        />
        <SoftwareInnerPages />
      </section>
      {/* end display pages */}
      {/* start awesome section */}
      {/* <section className="py-5">
              <div className="awesome-section">
                <SoftwareTitle />
                <SoftwareAwesomeSection />
              </div>
            </section> */}
      {/* end awesome section */}
      {/* { start Featured } */}
      {/* <section className="py-5">
        <SoftwareTitle
          title="Quiety Featured"
          subtitle="Enjoy some exclusive features"
        />
        <SoftwareFeature />
      </section> */}
      {/* end Featured */}
      {/* { start All designs } */}
      <section className="py-5">
        <SoftwareTitle
         title={t("software-design")}
          subtitle={t("software-design-desc")}
        />
        <SoftwareAllDesigns />
      </section>
      {/* end All designs */}
      {/* { start Testimonials } */}
      {/* <section className="py-5">
        <SoftwareTitle
          title={t("software-design")}
          subtitle={t("software-design-desc")}
        />
        <SoftwareTestimonials />
      </section> */}
      {/* end Testimonials */}
      {/* { start Testimonials } */}
      <footer>
        <SoftwareFooter />
      </footer>
      {/* end Testimonials */}</>)}
     
    </>
  );
}
