/* eslint-disable jsx-a11y/anchor-is-valid */
import { t } from "i18next";
import React from "react";
import { useTranslation } from "react-i18next";
import ft1 from "../../../assets/image/ft-banner-1.png";
import ft2 from "../../../assets/image/ft-banner-2.png";
import ft3 from "../../../assets/image/ft-banner-3.png";
import ft4 from "../../../assets/image/ft-banner-4.png";
export default function SoftwareFooter() {
  const { i18n } = useTranslation();
  return (
    <>
      <div className="software-footer overflow-hidden">
        <div className="footer-content d-flex justify-content-center align-items-center text-center flex-column">
          <span
            className={
              i18n.language === "  en" ? "footer-title" : "footer-title ar"
            }
          >
            {t("footer-content")}
          </span>
          <div className="footer-btn my-3">
            <button className="btn-pruchase text-capitalize">
              {t("button-Pruchase")}
            </button>
            <button className="btn-support text-capitalize">
              {t("button-support")}
            </button>
          </div>
          <p
            className={
              i18n.language === "  en"
                ? "text-light fs-6 mt-5"
                : "text-light fs-6 mt-5 ar"
            }
          >
            {t("copy-right")}
            <a
              className={
                i18n.language === "  en" ? `text-danger  ` : ` ar text-danger `
              }
            >
              {t("ThemeTags")}
            </a>
          </p>
        </div>
        <div
          className="software-footer__img d-none d-lg-block "
          data-aos="fade-left"
        >
          <img src={ft1} alt="" />
        </div>
        <div
          className="software-footer__img d-none d-lg-block "
          data-aos="fade-right"
        >
          <img src={ft2} alt="" />
        </div>
        <div
          className="software-footer__img d-none d-lg-block "
          data-aos="fade-left"
        >
          <img src={ft3} alt="" />
        </div>
        <div
          className="software-footer__img d-none d-lg-block "
          data-aos="fade-right"
        >
          <img src={ft4} alt="" />
        </div>
      </div>
    </>
  );
}
