import React, { createContext, useState } from "react";

export let loadingContext = createContext();
export default function LoadingContext(props) {
  const [isLoading, setIsLoading] = useState(false);
  const load = () => {
    console.log("sss");
    setIsLoading(true);
    setTimeout(() => {
      setIsLoading(false);
      console.log("aaa");
    }, 1200);
  };

  return (
    <>
      <loadingContext.Provider
        value={{
          load,
          isLoading,
        }}
      >
        {props.children}
      </loadingContext.Provider>
    </>
  );
}
