  import React, { useContext, useEffect, useState } from "react";
  import CaseStudies from "../../Components/ItSolutionComponents/CaseStudies/CaseStudies";
  import Elementor from "../../Components/ItSolutionComponents/Elementor/Elementor";
  import AOS from "aos";
  import "aos/dist/aos.css";
  import ItServices from "../../Components/ItSolutionComponents/ItServices/ItServices";
  import LastThinking from "../../Components/ItSolutionComponents/LastThinking/LastThinking";
  import OurAwards from "../../Components/ItSolutionComponents/OurAwards/OurAwards";
  import ItFooter from "../../Components/ItSolutionComponents/ItFooter/ItFooter";
  import ItSlider from "../../Components/ItSolutionComponents/ItSlider/ItSlider";
  import ItNavbar from "../../Components/ItSolutionComponents/ItNavbar/ItNavbar";
  import ScrollToTop from "../../Components/ScrollToTop/ScrollToTop";
  import Navbar from "../../Components/MasterPage/Navbar/Navbar";
  import { useTranslation } from "react-i18next";
  import { PuffLoader } from "react-spinners";
import LoadingScreen from "../../hooks/LoadingScreen";
import { loadingContext } from "../../context/LoadingContext";
import { RingLoader } from "react-spinners";

  export default function ItSolution() {
        const { i18n, t, languageDirection } = useTranslation();
      let { isLoading, load } = useContext(loadingContext);

    useEffect(() => {
      AOS.init({
        duration: 2000,
      });
      AOS.refresh();
    }, []);
    return (
      
        <>
          {isLoading ? (
            <div className="d-flex justify-content-center align-items-center vh-100 z-3">
              <RingLoader color="#2196F3" width="500" />
            </div>
          ) : (
            <>
              <ScrollToTop style="scroll-to-top it-color" />
              {/* <ItNavbar /> */}
              <Navbar />
              <ItSlider />
              {/* <CaseStudies />  */}
              <Elementor
                reviews={t("Reviewes")}
                title={t("Power-Solution-Title ")}
                fieldOne={t("Power-Solution-Content-Field-one")}
                fieldTwo={t("Power-Solution-Content-Field-two")}
                fieldThree={t("Power-Solution-content-field-three")}
              />
              <Elementor
                reviews={t("Reviewes")}
                title={t("NETWORKING-SOLUTIUONS-Title")}
                fieldOne={t("NETWORKING-SOLUTIUONS-Content-Field-one")}
                fieldTwo={t("NETWORKING-SOLUTIUONS-Content-Field-two")}
                fieldThree={t("NETWORKING-SOLUTIUONS-content-field-three")}
              />
              <Elementor
                reviews={t("Reviewes")}
                title={t("SURVEILLANCE-SYSTEMS-Title")}
                fieldOne={t("SURVEILLANCE-SYSTEMS-Content-Field-one")}
                fieldTwo={t("SURVEILLANCE-SYSTEMS-Content-Field-two")}
                fieldThree={t("SURVEILLANCE-SYSTEMS-content-field-three")}
              />
              <Elementor
                reviews={t("Reviewes")}
                title={t("FIRE-FIGHTING-&-FIRE-ALARM-SYSTEM-Title")}
                fieldOne={t(
                  "FIRE-FIGHTING-&-FIRE-ALARM-SYSTEM-Content-Field-one"
                )}
                fieldTwo={t(
                  "FIRE-FIGHTING-&-FIRE-ALARM-SYSTEM-Content-Field-two"
                )}
                fieldThree={t(
                  "FIRE-FIGHTING-&-FIRE-ALARM-SYSTEM-content-field-three"
                )}
              />
              <Elementor
                reviews={t("Reviewes")}
                title={t("PRINTING-SOLUTIONS-Title")}
                fieldOne={t("PRINTING-SOLUTIONS-Content-Field-one")}
                fieldTwo={t("PRINTING-SOLUTIONS-Content-Field-two")}
                fieldThree={t("PRINTING-SOLUTIONS-content-field-three")}
              />
              {/* <ItServices /> */}
              {/* <LastThinking /> */}
              <OurAwards />
              <ItFooter />
            </>
          )}
        </>
      
    );
  }
