import { useContext, useState } from "react";
import { useTranslation } from "react-i18next";
import { loadingContext } from "../context/LoadingContext";

export default function useTranslate() {
  const { load } = useContext(loadingContext);
  // const [isLoading, setIsLoading] = useState(false);
  const { t, i18n } = useTranslation();
  const [languageDirection, setLanguageDirection] = useState(i18n.dir());
const handelChangeLng = async (lng) => {
    try {
       load();
    await i18n.changeLanguage(lng);
      // localStorage.setItem("lng", lng);
      // setLanguageDirection(i18n.dir());
      // document.body.dir = i18n.dir();
    } catch (error) {
      console.error("Error in handelChangeLng:", error);
    }
  };
  
  return { languageDirection, i18n, t, handelChangeLng };
};

