import React from "react";
export default function ItFooter() {
  return (
    <>
      <footer>
        <div className="it-footer">
          <div className="container">
            <div className="it-footer-content ">
              <div className="row align-items-center py-5 ">
                <div className="col-lg-6">
                  <div className="subscribe-desc py-5">
                    <h3 className="subscribe-title" data-aos="fade-right">
                      Join The <span className="dif-text">ITSulu</span>{" "}
                      Experience
                    </h3>
                    <span className="desc-text" data-aos="zoom-in-up">
                      Duis autem vel eum iriure dolor in hendrerit in vulputate.
                    </span>
                  </div>
                </div>
                <div className="col-lg-6">
                  <div className="subscribe-input">
                    <input
                      type="email"
                      placeholder="Your email address"
                      className="email-input ps-5"
                    />
                    <div className="sub-button text-center">
                      <span className="btn-text">Subscribe Now</span>
                    </div>
                  </div>
                </div>
              </div>
              <div className="layer"></div>
            </div>
          </div>
        </div>
      </footer>
    </>
  );
}
