import React, { useContext, useRef, useState } from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css/pagination";
import { Pagination, Mousewheel, Parallax } from "swiper/modules";
import logo from "../../assets/image/logo.png";
import one from "../../assets/image/one.png";
import two from "../../assets/image/two.png";
import three from "../../assets/image/three.png";
import four from "../../assets/image/four.png";
import five from "../../assets/image/five.png";
import six from "../../assets/image/six.png";
import markOne from "../../assets/image/mark-one (1).png";
import markTwo from "../../assets/image/mark-one (2).png";
import markThree from "../../assets/image/mark-one (3).png";
import markFalse from "../../assets/image/false.png";
import magnifier from "../../assets/image/magnifier.png";
import man from "../../assets/image/man.png";
import lcd from "../../assets/image/lcd.png";
import report from "../../assets/image/report.png";
import student from "../../assets/image/8294 [Converted]-01.png";
import bgBlue from "../../assets/image/8294 [Converted]-02.png";
import teacher from "../../assets/image/8294 [Converted]-03.png";
import display from "../../assets/video/Signal _ Technology Opener (After Effects Template) ★ AE Templates.mp4";
import { useDispatch, useSelector } from "react-redux";
import FloatinDiv from "../../Components/MasterPage/FloatinDiv/FloatinDiv";
import Navbar from "../../Components/MasterPage/Navbar/Navbar";
import useTranslate from "../../hooks/useTranslate";
import { useTranslation } from "react-i18next";
import { loadingContext } from "../../context/LoadingContext";
import { RingLoader } from "react-spinners";


export default function Home() {
  // useSelector((state)=>console.log(state))
  // const menu = ["Home", "Our Services", "Projects", "Tranning", "Consultation", "Software Solution", "System Integration"];
              const { i18n, t, languageDirection } = useTranslation();
              let { isLoading, load } = useContext(loadingContext);
  const pagination = {
    el: ".swiper-pagination ",
    clickable: true,
    renderBullet: function (index, className) {
      return '<span class="' + className + '">' + "</span>";
    },
  };

  return (
    <>
      {isLoading ? (
        <div className="d-flex justify-content-center align-items-center vh-100 z-3">
          <RingLoader color="#2196F3" width="500" />
        </div>
      ) : (
        <div className="home-page">
          <Navbar />

          <Swiper
            mousewheel={{
              releaseOnEdges: true,
            }}
            pagination={pagination}
            modules={[Pagination, Mousewheel, Parallax]}
            className="mySwiper"
            parallax={true}
            speed={2000}
            slidesToScroll={10}
            key={i18n.language}
            dir={i18n.language === "en" ? "ltr" : "rtl"}
          >
            {/* first Slide */}

            <SwiperSlide className="first-slide">
              <div className="banner-slide ">
                {" "}
                <img
                  src={logo}
                  alt=""
                  data-swiper-parallax="-600"
                  data-swiper-parallax-opacity="-2000"
                  data-swiper-parallax-duration="7000"
                />
              </div>
              <div className="video-content ">
                <video
                  controls
                  autoPlay
                  muted
                  loop
                  className="d-none d-xl-block"
                >
                  <source src={display} />
                </video>
                <div className="video-layer "></div>
              </div>
            </SwiperSlide>
            {/* second Slide */}
            <SwiperSlide className="second-slide">
              <div className="software">
                <div className="row align-content-center vh-100">
                  <div className="col-md-5 offset-lg-1 ">
                    <div className="title text-lg-start">
                      <h3
                        className="title__content text-light"
                        data-swiper-parallax="-600"
                        data-swiper-parallax-opacity="-2000"
                        data-swiper-parallax-duration="7000"
                      >
                        {" "}
                        {t("software-content")}
                      </h3>
                    </div>
                  </div>
                  <div className="col-md-6 d-none d-md-block h-75  position-relative ">
                    <div className="video-box"></div>
                    <div className="bg-layer"></div>
                  </div>
                </div>
                <div className="rectangle-img"></div>
              </div>
              <FloatinDiv
                className="live-button "
                text="go live"
                webSiteLink="./software"
              />
            </SwiperSlide>
            {/* third Slide */}
            <SwiperSlide className="third-slide">
              <div className="it-solution">
                <div className="row align-content-center ">
                  <div className="col-md-5 offset-lg-1 ">
                    <div className="title text-lg-start">
                      <h3
                        className="title__content text-light"
                        data-swiper-parallax="-600"
                        data-swiper-parallax-opacity="-2000"
                        data-swiper-parallax-duration="7000"
                      >
                        {" "}
                        {t("software-content")}
                      </h3>
                    </div>
                  </div>
                  <div className="col-md-6 d-none d-md-block position-relative h-75  ">
                    <div className="img-box">
                      <img
                        src={one}
                        alt=""
                        data-swiper-parallax-y="-600"
                        data-swiper-parallax-opacity="-2000"
                        data-swiper-parallax-duration="2000"
                      />
                      <img
                        src={two}
                        alt=""
                        data-swiper-parallax-y="-600"
                        data-swiper-parallax-opacity="-2000"
                        data-swiper-parallax-duration="3000"
                      />
                      <img
                        src={three}
                        alt=""
                        data-swiper-parallax-y="-600"
                        data-swiper-parallax-opacity="-2000"
                        data-swiper-parallax-duration="4000"
                      />
                      <img
                        src={four}
                        alt=""
                        data-swiper-parallax-y="-600"
                        data-swiper-parallax-opacity="-2000"
                        data-swiper-parallax-duration="5000"
                      />
                      <img
                        src={five}
                        alt=""
                        data-swiper-parallax-y="-600"
                        data-swiper-parallax-opacity="-2000"
                        data-swiper-parallax-duration="6000"
                      />
                      <img
                        src={six}
                        alt=""
                        data-swiper-parallax-y="-600"
                        data-swiper-parallax-opacity="-2000"
                        data-swiper-parallax-duration="7000"
                      />
                    </div>
                  </div>
                </div>
              </div>
              <FloatinDiv
                className="live-button "
                text="go live"
                webSiteLink="./itsolution"
              />
            </SwiperSlide>
            {/* fourth Slide */}
            <SwiperSlide className="fourth-slide">
              <div className="services">
                <div className="row align-content-center vh-100">
                  <div className="col-md-5 offset-lg-1 ">
                    <div className="title text-lg-start">
                      <h3
                        className="title__content text-light"
                        data-swiper-parallax="-600"
                        data-swiper-parallax-opacity="-2000"
                        data-swiper-parallax-duration="7000"
                      >
                        {" "}
                        {t("software-content")}
                      </h3>
                    </div>
                  </div>
                  <div className="col-md-6 d-none d-md-block position-relative h-75  ">
                    <div className="video-box"></div>
                  </div>
                </div>
              </div>
              <FloatinDiv className="live-button " text="go live" />
            </SwiperSlide>
            {/* fifth Slide */}
            <SwiperSlide className="fifth-slide overflow-hidden">
              <div className="events">
                <div className="row align-content-center events__content  ">
                  <div className="col-md-5 offset-lg-1 ">
                    <div className="title text-lg-start">
                      <h3
                        className="title__content text-light"
                        data-swiper-parallax="-600"
                        data-swiper-parallax-opacity="-2000"
                        data-swiper-parallax-duration="7000"
                      >
                        {" "}
                        {t("software-content")}
                      </h3>
                      <span className="subTitle">
                        Go to departement website
                      </span>
                    </div>
                  </div>
                  <div className="col-md-6 d-none d-md-block  position-relative  ">
                    <div className="img-box">
                      <img
                        src={teacher}
                        alt=""
                        data-swiper-parallax="-600"
                        data-swiper-parallax-opacity="-2000"
                        data-swiper-parallax-duration="7000"
                      />
                      <img
                        src={student}
                        alt=""
                        data-swiper-parallax="600"
                        data-swiper-parallax-opacity="-2000"
                        data-swiper-parallax-duration="7000"
                      />
                      <img
                        src={bgBlue}
                        alt=""
                        data-swiper-parallax-y="-600"
                        data-swiper-parallax-opacity="-2000"
                        data-swiper-parallax-duration="7000"
                      />
                    </div>
                  </div>
                </div>
              </div>
              <FloatinDiv className="live__button " text="go live" />
            </SwiperSlide>
            {/* sixth Slide */}
            <SwiperSlide className="sixth-slide overflow-hidden">
              <div className="hr">
                <div className="row align-content-center hr__content  ">
                  <div className="col-md-5 offset-lg-1 ">
                    <div className="title text-lg-start">
                      <h3
                        className="title__content text-light"
                        data-swiper-parallax="-600"
                        data-swiper-parallax-opacity="-2000"
                        data-swiper-parallax-duration="7000"
                      >
                        {" "}
                        {t("software-content")}
                      </h3>
                      <span className="subTitle">
                        Go to departement website
                      </span>
                    </div>
                  </div>
                  <div className="col-md-6 d-none d-md-block  position-relative  ">
                    <div className="img-box">
                      <img
                        src={markOne}
                        alt=""
                        data-swiper-parallax="-600"
                        data-swiper-parallax-opacity="-2000"
                        data-swiper-parallax-duration="7000"
                      />
                      <img
                        src={markTwo}
                        alt=""
                        data-swiper-parallax="600"
                        data-swiper-parallax-opacity="-2000"
                        data-swiper-parallax-duration="7000"
                      />
                      <img
                        src={markThree}
                        alt=""
                        data-swiper-parallax="600"
                        data-swiper-parallax-opacity="-2000"
                        data-swiper-parallax-duration="7000"
                      />
                      <img
                        src={markFalse}
                        alt=""
                        data-swiper-parallax="400"
                        data-swiper-parallax-opacity="-2000"
                        data-swiper-parallax-duration="7000"
                      />
                      <img
                        src={magnifier}
                        alt=""
                        data-swiper-parallax-y="-600"
                        data-swiper-parallax-opacity="-2000"
                        data-swiper-parallax-duration="4000"
                        data-swiper-parallax-scale="5"
                      />
                      <img
                        src={man}
                        alt=""
                        data-swiper-parallax="-600"
                        data-swiper-parallax-opacity="-2000"
                        data-swiper-parallax-duration="7000"
                      />
                      <img
                        src={lcd}
                        alt=""
                        data-swiper-parallax-y="-600"
                        data-swiper-parallax-opacity="-2000"
                        data-swiper-parallax-duration="7000"
                      />
                      <img
                        src={report}
                        alt=""
                        data-swiper-parallax-y="-600"
                        data-swiper-parallax-opacity="-2000"
                        data-swiper-parallax-duration="7000"
                      />
                    </div>
                  </div>
                </div>
              </div>
              <FloatinDiv className="live__button " text="go live" />
            </SwiperSlide>
            <div className="swiper-pagination d-none d-md-block"></div>
          </Swiper>
        </div>
      )}
    </>
  );
}
