import i18next from "i18next";
import {initReactI18next} from 'react-i18next'
import en from './local/en.json'
import ar from './local/ar.json'
i18next.use(initReactI18next).init({
  resources: {
    en: {
      translation: en,
    },
    ar: {
      translation: ar,
    },
  },
  
  lng: "en",
});
