import { Suspense, useEffect } from "react";
import { t } from "i18next";
import React from "react";
import team from "../../../assets/image/It-Solution/8.jpg";
import f1 from "../../../assets/image/It-Solution/f1-200x206.jpg";
import f2 from "../../../assets/image/It-Solution/f2-200x189.jpg";
import f3 from "../../../assets/image/It-Solution/f3-200x192.jpg";
import f4 from "../../../assets/image/It-Solution/f4-200x207.jpg";
import { useTranslation } from "react-i18next";
export default function Elementor({ reviews  ,title ,fieldOne , fieldTwo , fieldThree }) {
  const { i18n, isloading, t, languageDirection } = useTranslation();

  return (
    <>
      <Suspense fallback="loading">
        <section>
          <div
            className={
              i18n.language === "en"
                ? `ltr py-5 overflow-hidden ${languageDirection}`
                : `rtl py-5 overflow-hidden ${languageDirection}`
            }
          >
            <div className="container">
              <div className="row">
                <div className="col-lg-6 position-relative ">
                  <div className="elmentor-design">
                    <div className="elementor__img  overflow-hidden rounded-4 ">
                      <img src={team} alt="" />
                    </div>
                    <div
                      className="float-window p-4 rounded-3 "
                      data-aos="fade-left"
                    >
                      <ul className="list-inline speakers   ">
                        <li className="list-inline-item m-0">
                          <img src={f1} alt="" className="img-fluid " />
                        </li>
                        <li className="list-inline-item m-0">
                          <img src={f2} alt="" className="img-fluid " />
                        </li>
                        <li className="list-inline-item m-0">
                          <img src={f3} alt="" className="img-fluid " />
                        </li>
                        <li className="list-inline-item m-0">
                          <img src={f4} alt="" className="img-fluid " />
                        </li>
                      </ul>
                      <div className="row align-items-center float-window__footer ">
                        <div className="col-3 text-end">
                          <span className="fs-2 ">4.5</span>
                        </div>
                        <div className="col-9 text-start">
                          <i className="fa-solid fa-star"></i>
                          <i className="fa-solid fa-star"></i>
                          <i className="fa-solid fa-star"></i>
                          <i className="fa-solid fa-star"></i>
                          <i className="fa-solid fa-star text-light-emphasis"></i>
                          <p className="">{reviews}</p>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="col-lg-6  ps-lg-5 ">
                  <div className="elmentor-description">
                    <div className="elementor-content">
                      <h2
                        className="h1 py-2"
                        data-aos="fade-up"
                        data-aos-anchor-placement="top-bottom"
                      >
                        {title}
                      </h2>
                    </div>
                    <ul className="list-inline elmenetor-wedget-container">
                      <li className="list-inline-items fs-5 my-3 ps-2">
                        <span data-aos="fade-left">
                          {fieldOne}
                        </span>
                      </li>
                      <li className="list-inline-items fs-5 my-3 ps-2">
                        <span data-aos="fade-left">
                          {fieldTwo}
                        </span>
                      </li>
                      <li className="list-inline-items fs-5 my-3 ps-2">
                        <span data-aos="fade-left">
                          {fieldThree}
                        </span>
                      </li>
                    </ul>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </Suspense>
    </>
  );
}
