import React, { useContext, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import useTranslate from "../../../hooks/useTranslate";
import logo from "../../../assets/image/It-Solution/logo.png";
import f1 from "../../../assets/image/icons8-egypt-48.png";
import LoadingScreen from "../../../hooks/LoadingScreen";
import { loadingContext } from "../../../context/LoadingContext";

export default function Navbar() {
    const [navbar, setNavbar] = useState(false);
  const [menu, setMenu] = useState(false);
  const { handelChangeLng } = useTranslate();
  const { load, isLoading,i18n } = useContext(loadingContext);
    useEffect(() => {
      window.addEventListener("scroll", () => {
        if (window.scrollY >= 600) {
          setNavbar(true);
        } else {
          setNavbar(false);
        }
      });
    }, []);
  return (
    <>
      {/* navbar */}

      <div
        className={
          navbar
            ? "navbar position-fixed  z-3 bg-light"
            : "navbar position-fixed  z-3 "
        }
      >
        <div className="container d-block ">
          <div className="row ">
            <div className="col-6 ">
              <img src={logo} className="img-logo" alt="" />
            </div>
            <div className="col-6 py-2 d-flex align-items-center justify-content-end ">
              <select
                className={navbar ? "language" : "language-dif"}
                name="language"
                id="language"
                onChange={({ target: { value } }) => {
                  handelChangeLng(value);
                }}
                isMulti={false}
              >
                <option value="">language</option>
                <option value="en">English</option>
                <option value="ar">Arabic</option>
              </select>
              <i className=" mx-3 fs-4 align-middle text-light fa-solid fa-circle-half-stroke"></i>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
