import React from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css/effect-fade";
import "swiper/css/navigation";

import {
  Pagination,
  Mousewheel,
  Parallax,
  Autoplay,
  FreeMode,
  EffectFade,
  Navigation,
} from "swiper/modules";
import s1 from "../../../assets/image/It-Solution/3-3.jpg";
import s2 from "../../../assets/image/It-Solution/5.jpg";
import s3 from "../../../assets/image/It-Solution/6.jpg";
export default function ItSlider() {
  return (
    <>
      <div className="it-slider">
        <Swiper
          spaceBetween={30}
          effect="fade"
          navigation={true}
          speed="2000"
          loop={true}
          modules={[EffectFade, Navigation, Pagination, Parallax, Autoplay]}
          parallax={true}
          autoplay={{
            delay: 2500,
            disableOnInteraction: false,
          }}
          className="mySwiper"
        >
          <SwiperSlide>
            <div className="slide ">
              <img src={s1} className="img-fluid" alt="" />
              <div
                className="slide-content text-center"
                data-swiper-parallax-y="50%"
                data-swiper-parallax-opacity="-2000"
                data-swiper-parallax-duration="3000"
              >
                {/* The Freedom to focus on improving Your Technology  */}
                <h3 className="slide-title ">
                  our <span className="dif-color">inspiration</span>
                </h3>
                <h4 className="slide-subtitle py-5">
                  the
                  <span className="dif-style"> Freedom</span> Your Business with{" "}
                  <span className="dif-style"> Technology </span>
                </h4>
                <p className="slide-desc">
                  Top of cutting-edge technology trends providing great customer{" "}
                  <br />
                  experience, businesses can create a strong brand.
                </p>
              </div>
            </div>
          </SwiperSlide>
          <SwiperSlide>
            <div className="slide">
              <img src={s2} className="img-fluid" alt="" />
              <div
                className="slide-content text-center"
                data-swiper-parallax-y="50%"
                data-swiper-parallax-opacity="-2000"
                data-swiper-parallax-duration="3000"
              >
                {/*  Enabling Remote Business Operations with Cloud-Based  */}
                <h3 className="slide-title ">
                  our <span className="dif-color">Mission</span>
                </h3>
                <h4 className="slide-subtitle py-5">
                  Enabling
                  <span className="dif-style"> Remote</span> Business
                  <span className="dif-style"> Operations </span>
                  with Cloud-Based
                </h4>
                <p className="slide-desc">
                  Cloud-based solutions have allowed businesses to shift their
                  operations to remote <br />
                  allowing employees to work with an internet connection.
                </p>
              </div>
            </div>
          </SwiperSlide>
          <SwiperSlide>
            <div className="slide">
              <img src={s3} className="img-fluid" alt="" />
              <div
                className="slide-content text-center"
                data-swiper-parallax-y="50%"
                data-swiper-parallax-opacity="-2000"
                data-swiper-parallax-duration="3000"
              >
                {/* Transforming Your Business with Emerging Technologies */}
                <h3 className="slide-title ">
                  our <span className="dif-color">projects</span>
                </h3>
                <h4 className="slide-subtitle py-5">
                  <span className="dif-style">Transforming</span> Your Business
                  with <span className="dif-style"> Emerging </span>
                  Technologies
                </h4>
                <p className="slide-desc">
                  Emerging technologies such as artificial intelligence,
                  blockchain and the <br />
                  IoT are transforming the business landscape.
                </p>
              </div>
            </div>
          </SwiperSlide>
        </Swiper>
      </div>
    </>
  );
}
